*{
    font-family: sans-serif;
    box-sizing: border-box; 
}

.LogInBox{
    width: 600px;
    height: 450px;
    display: flex;
    flex-direction: column;
    margin: auto;
    /* margin-top: 1%; */
    background: rgba(234,234,234,0.3);
    padding-bottom: 30px
}

.LogInBox p {
    text-align: center;
    color: whitesmoke;
}

.Header{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 6px;
    width: 100%;
    margin-top: 30px;
}

.Title{
    color:rgb(118, 8, 27);
    font-size: 35px;
    font-weight: 700;
}

.UnderLine{
    width: 80px;
    height: 3px;
    background-color: rgb(118, 8, 27);
    border-radius: 9px;
}

.Form{
    margin-top:40px;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.Input{
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 60px;
    background: rgb(234, 234, 234);
    border-radius: 6px;
}

.Input img {
    margin: 0px 20px;
    width: 30px;
}

.Input input {
    height: 50px;
    width: 400px;
    background: transparent;
    border: none;
    outline: none;
    color: #797979;
    font-size: 19px;
}

.Login{
    padding-left: 62px;
    margin-top: 15px;
    /* color: #797979; */
    color: whitesmoke;
    font-size: 18px;
}

.Login span{
    color: rgb(118, 8, 27);
    cursor: pointer;
}

.SubmitSection{
    display: flex;
    justify-content: space-between;
    gap:30px;
    margin: 8% 10%;
}

.Button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 40px;
    color: whitesmoke;
    background: rgb(118, 8, 27);
    border-radius: 20px;
    border: none;
    font-size: 19px;
    font-weight: 500;
    cursor: pointer;
}

@media screen and (max-width:650px) {
    .LogInBox{
        width: 90%;
        height: 350px;
        display: flex;
        flex-direction: column;
        margin: auto;
        background: rgba(234,234,234,0.3);
        padding-bottom: 10px
    }
    
    .Header{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2px;
        width: 100%;
        margin-top: 10px;
    }
    
    .Title{
        color:rgb(118, 8, 27);
        font-size: 25px;
        font-weight: 700;
    }
    
    .UnderLine{
        width: 60px;
        height: 1px;
        background-color: rgb(118, 8, 27);
        border-radius: 8px;
    }
    
    .Form{
        margin-top:20px;
        display: flex;
        flex-direction: column;
        gap: 6px;
    }
    
    .Input{
        display: flex;
        align-items: center;
        margin: auto;
        width: 90%;
        height: 40px;
        background: rgb(234, 234, 234);
        border-radius: 5px;
    }
    
    .Input img {
        margin: 0px 10px;
        width: 20px;
    }
    
    .Input input {
        height: 50px;
        width: 300px;
        background: transparent;
        border: none;
        outline: none;
        color: #797979;
        font-size: 15px;
    }
    
    .Login{
        padding-left: 6%;
        margin-top: 15px;
        color: whitesmoke;
        font-size: 15px;
    }
    
    .Login span{
        color: rgb(118, 8, 27);
        cursor: pointer;
    }
    
    .SubmitSection{
        display: flex;
        justify-content: space-between;
        gap:30px;
        margin: 5% 8%;
    }
    
    .Button{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100px;
        height: 35px;
        color: whitesmoke;
        background: rgb(118, 8, 27);
        border-radius: 15px;
        border: none;
        font-size: 14px;
        font-weight: 400;
        cursor: pointer;
    }
    
}